.popUpConfirm {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 12;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  padding: 0 30px;
  background: rgba(0, 0, 0, .6);

  .jump {
    width: 100%;
    background: #FFFFFF;
    border-radius: 2px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .description {
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.5px;
      margin-bottom: 15px;
      margin-top: 10px;
      font-family: PingFangSC-Regular, serif;
    }

    .button {
      width: 160px;
      height: 36px;
      font-size: 14px;
    }

    .customConfirm {
      display: flex;
      height: 40px;
      width: 100%;
      border-radius: 2px;
      justify-content: center;
      align-items: center;
      transition: all 200ms ease;
      border: none;
      background: #636ACC;

      span {
        color: #FFFFFF;
        font-size: 14px;
      }
    }

  }

  .boxDesc {
    font-size: 14px;
    color: #626acc;
    letter-spacing: 0;
    line-height: 20px;
  }
}
