.insetContainer{
  &.addressBook{
    overflow:auto;
    padding:0;
    //background:transparent url('../../assets/images/new/background.svg') left -130px no-repeat;
    .back{
      background-image:url('../../assets/images/new/icon-back2.svg');
    }
    .pageHeader{
      position: relative;
      height: 56px;
      display: flex;
      flex-direction:row;
      align-items:center;
      justify-content:center;
      .addItem{
        cursor: pointer;
        position: absolute;
        right: 10px;
        width: 40px;
        height: 56px;
        background-image: url('../../assets/images/new/icon-add-address.svg');
        background-repeat: no-repeat;
        background-position: center center;
      }
    }
    .greyModal{
      overflow: auto;
      padding: 15px 0 0;
      background-color: transparent;
      display: flex;
      flex-direction:column;
      flex:1;
      .book{
        padding:0 15px 0 5px;
        flex:1;
        .item{
          margin-top: 10px;
          width: 100%;
          background: #FFFFFF;
          box-shadow: 0 2px 16px 0 rgba(136,137,152,0.10);
          border-radius: 4px;
          display: flex;
          flex-direction: column;
          cursor: pointer;
          .r2{
            padding:5px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            &:hover{
              background-color: #F4F5F8;
            }
            &:last-child{
              font-size: 14px;
              color: #888998;
            }
            .addressName{
              margin-top: 4px;
              height: 22px;
              //font-family: PingFangSC-Regular, serif;
              font-size: 15px;
              color: #505160;
              .search{
                color: #65AAEF;
              }
            }
            .address{
              margin-top: 4px;
              height: 19px;
              //font-family: PingFangSC-Regular, serif;
              font-size: 12px;
              color: #888998;
              .search{
                color: #65AAEF;
              }
            }
            .remarks{
              margin-top: 4px;
              height: 12px;
              //font-family: PingFangSC-Regular, serif;
              font-size: 12px;
              color: #505160;
              -webkit-transform-origin-x: 0;
              -webkit-transform: scale(0.75);
            }
          }
          &:hover{
            .url {
              .delete {
                opacity: 1;
              }
            }
          }
          .url,.row{
            padding-left: 10px;
            display: flex;
            flex-direction: row;
            align-items: center;
            & > span{
              &:nth-child(1){
                margin-right: 10px;
              }
            }
            & > a{
              color: #515060;
              &:hover {
                color: #0006ec;
                text-decoration: underline;
              }
            }
          }
          .row{
            height:30px;
            font-size: 14px;
            color: #888998;
          }
        }
        .blankSearch{
          text-align: center;
          display: block;
          line-height: 400px;
          font-size: 14px;
          color: #999;
        }
      }
      .addNode{
        margin: 16px 25px;
        height: 50px;
        border: 1px solid rgba(80,81,96,0.10);
        border-radius: 2px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        color: #515060;
        cursor: pointer;
        &:before{
          content: '';
          width: 11px;
          height: 11px;
          background-image: url('../../assets/images/new/side/icon-add.svg');
          margin-right:10px;
          cursor: pointer;
        }
      }
      .noData{
        color: #fff;
        font-size: 14px;
        height: 260px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}





